import React, { useState } from 'react';
import axios from 'axios';


function Form() {
  const [formData, setFormData] = useState({
    email: '',
    message: '',
  });

  const [popupMessage, setPopupMessage] = useState('');

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({ ...prevData, [name]: value }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    // Validation des champs requis
    if (!formData.email || !formData.message) {
      setPopupMessage('Veuillez remplir tous les champs.');
      return;
    }

    try {
      // Envoi des données au serveur
      const response = await axios.post('URL_DU_SERVEUR', formData);

      // Gérer la réponse du serveur
      console.log(response.data);
      // Afficher un message de succès à l'utilisateur ici

    } catch (error) {
      // Gérer les erreurs, par exemple afficher un message d'erreur à l'utilisateur
      console.error('Erreur lors de l\'envoi de l\'e-mail :', error);
      setPopupMessage('Une erreur est survenue. Veuillez réessayer plus tard.');
    }
  };

  const handlePopupClose = () => {
    setPopupMessage('');
  };

  return (
    <div className='form'>
      {popupMessage && (
        <div className="popup-container">
          <div className="popup">
            <p>{popupMessage}</p>
            <button onClick={handlePopupClose}>Fermer</button>
          </div>
        </div>
      )}
      <form className='formulaire' onSubmit={handleSubmit}>
        <input
          type="email"
          id="email"
          name="email"
          value={formData.email}
          onChange={handleChange}
          placeholder='votreMail@exemple.com'
        />
        <textarea
          id='text'
          name="message"
          value={formData.message}
          onChange={handleChange}
          cols="20"
          rows="10"
          placeholder='votre message'
        />
        <input type="submit" value="Envoyer" />
      </form>
    </div>
  );
}

export default Form;
